import * as React from 'react'
import _ from 'lodash'
import { FaExternalLinkAlt, FaMapMarker } from 'react-icons/fa'
import { MdLocationOn, MdAccessTime } from 'react-icons/md'
import dateFns from 'date-fns'
import styled from 'styled-components'
import { graphql } from 'gatsby'

import Layout from '@components/Layout'
import UI, { Box, H4 } from '@components/UI'
import { Query } from '@generated/graphql'

import { Theme } from '../style/theme'

const Date = styled(H4).attrs({
  pb: 1,
  color: 'black'
})`
  text-transform: uppercase;
`

const CityCountry = styled(H4).attrs({
  color: 'primary',
  pb: 3
})`
  text-transform: uppercase;
`

const Time = styled(H4).attrs({
  color: 'dark',
  pb: 1
})`
  text-transform: uppercase;
`

const Description = styled(H4).attrs({
  mb: 1,
  color: 'dark'
})``

const Link = styled(UI.Link).attrs({
  fontFamily: 'sans'
})``

const EventBox = styled(Box).attrs({
  pb: 4,
  mb: 4,
  borderStyle: 'solid',
  borderWidth: 0,
  borderTop: 0,
  borderX: 0,
  borderColor: 'gray'
})``

const Event = ({ event: { dateObj, venue, city, country, link } }) => (
  <Link href={link}>
    <EventBox>
      <Date>{dateFns.format(dateObj, 'ddd, D MMM YYYY')}</Date>
      <CityCountry>{`${city}, ${country}`}</CityCountry>
      <Time>
        <MdAccessTime />
        {` ${dateFns.format(dateObj, 'h:mm A')}`}
      </Time>
      <Box>
        <Description>
          <MdLocationOn style={{ verticalAlign: 'left' }} />
          {` ${venue}`}
        </Description>
      </Box>

      {link ? (
        <Description>
          <FaExternalLinkAlt />
          {` ${link}`}
        </Description>
      ) : null}
    </EventBox>
  </Link>
)

const Events = ({ data }: { data: Query }) => {
  const events = _.chain(data.allMarkdownRemark.nodes[0].frontmatter.events)
    .map(({ date, time, venue, city, country, link }) => ({
      dateObj: dateFns.parse(`${date} ${time}`),
      venue,
      city,
      country,
      link
    }))
    .partition(({ dateObj }) => dateFns.isPast(dateObj))
    .value()

  const pastEvents = events[0]
  const upcomingEvents = events[1]

  return (
    <Layout>
      {_.map(upcomingEvents, event => (
        <Event event={event} />
      ))}
      {_.map(pastEvents, event => (
        <Event event={event} />
      ))}
    </Layout>
  )
}

export default Events

export const query = graphql`
  {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/content.events/" } }
    ) {
      nodes {
        frontmatter {
          events {
            date
            time
            venue
            city
            country
            link
          }
        }
      }
    }
  }
`
